class Basics extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      web3: "",
      coinbase: "",
      camera: false,
      answer: {},
      auth: false,
      visible: false
    };
  }

  componentDidMount = async () => {
    try {
      const web3 = await getWeb3();

      const coinbase = await web3.eth.getCoinbase();

      if (this.props.getCurrentProvider(web3)) {
        const self = this;

        window.ethereum.on("accountsChanged", function(accounts) {
          self.setState({ coinbase: accounts[0] });
        });
      }

      let answer = await this.newResearch();
      this.setState({
        web3,
        coinbase,
        answer
      });
    } catch (error) {
      // Catch any errors for any of the above operations.
      alert(
        `Failed to load web3, accounts, or contract. Check console for details.`
      );
      console.error(error);
    }
  };

  newResearch = () => {
    const answer = {
      researchID: null,
      enrolledID: null,
      surveys: {}
    };

    for (let i = 0, cnt = 0; i < DATA.steps.length; i++) {
      if (DATA.steps[i].surveys) {
        answer.surveys["step_" + cnt] = {};
        for (const [j, item] of DATA.steps[i].surveys.entries()) {
          if (item.group) {
            answer.surveys["step_" + cnt]["answer_" + j] = {};
            for (const [k] of item.group.entries()) {
              answer.surveys["step_" + cnt]["answer_" + j]["sub_" + k] = null;
            }
          } else {
            answer.surveys["step_" + cnt]["answer_" + j] = null;
          }
        }
        cnt++;
      }
    }

    return answer;
  };

  onCamera = () => {
    this.setState({ camera: true });
  };

  handleScan = data => {
    if (data) {
      this.setState({ camera: false });
      let params = JSON.parse(data);
      console.log(params.id, this.props.ethrDid);
      let msg, color, auth;

      if (params.id === this.props.ethrDid.toLowerCase()) {
        msg = "인증 되었습니다.";
        color = "success";
        auth = true;
      } else {
        msg = "잘못된 접근입니다.";
        color = "danger";
        auth = false;
      }

      this.onShowAlert(msg, color, auth);
    }
  };

  handleError = err => {
    console.error(err);
  };

  handleChange = e => {
    const temp = e.target.name.split(":");

    switch (parseInt(temp[0])) {
      case 0:
      case 1:
        this.setState({
          answer: update(this.state.answer, {
            surveys: {
              ["step_" + temp[1]]: {
                ["answer_" + temp[2]]: { $set: e.target.value }
              }
            }
          })
        });

        break;
      case 2:
        if (temp.length === 4) {
          this.setState({
            answer: update(this.state.answer, {
              surveys: {
                ["step_" + temp[1]]: {
                  ["answer_" + temp[2]]: {
                    ["sub_" + temp[3]]: { $set: parseInt(e.target.value) }
                  }
                }
              }
            })
          });
        } else {
          this.setState({
            answer: update(this.state.answer, {
              surveys: {
                ["step_" + temp[1]]: {
                  ["answer_" + temp[2]]: {
                    $set: e.target.value.checked
                      ? parseInt(e.target.value)
                      : e.target.value
                  }
                }
              }
            })
          });
        }
        break;
      case 3:
        let etv = e.target.value;
        let etch = e.target.checked;

        if (temp.length === 4) {
          // group
        } else {
          if (
            !this.state.answer.surveys["step_" + temp[1]]["answer_" + temp[2]]
          ) {
            this.setState(
              {
                answer: update(this.state.answer, {
                  surveys: {
                    ["step_" + temp[1]]: {
                      ["answer_" + temp[2]]: { $set: { check: {}, etc: null } }
                    }
                  }
                })
              },
              () => {
                if (isNaN(etv)) {
                  this.setState({
                    answer: update(this.state.answer, {
                      surveys: {
                        ["step_" + temp[1]]: {
                          ["answer_" + temp[2]]: {
                            etc: {
                              $set: etv
                            }
                          }
                        }
                      }
                    })
                  });
                } else {
                  this.setState({
                    answer: update(this.state.answer, {
                      surveys: {
                        ["step_" + temp[1]]: {
                          ["answer_" + temp[2]]: {
                            check: { [etv]: { $set: etch } }
                          }
                        }
                      }
                    })
                  });
                }
              }
            );
          } else {
            if (isNaN(etv)) {
              this.setState({
                answer: update(this.state.answer, {
                  surveys: {
                    ["step_" + temp[1]]: {
                      ["answer_" + temp[2]]: {
                        etc: {
                          $set: etv
                        }
                      }
                    }
                  }
                })
              });
            } else {
              this.setState({
                answer: update(this.state.answer, {
                  surveys: {
                    ["step_" + temp[1]]: {
                      ["answer_" + temp[2]]: {
                        check: { [etv]: { $set: etch } }
                      }
                    }
                  }
                })
              });
            }
          }
        }

        break;
      case 4:
        if (e.target.checked) {
          this.setState({
            answer: update(this.state.answer, {
              surveys: {
                ["step_" + temp[1]]: {
                  ["answer_" + temp[2]]: { $set: parseInt(e.target.value) }
                }
              }
            })
          });
        }
        break;
      default:
        break;
    }
  };

  getQuestion(index, item) {
    return index + 1 + ". " + item.question;
  }

  submit = () => {
    this.props.toggleScreen();

    console.log(this.state.answer);
    console.log(JSON.stringify(this.state.answer));

    // 이더리움 로그인 부분, QR코드(?) 혹은 유니크한 일련 번호를 입력받아 해당 정보와 함께 사인하여 서버에 업로드 하는 부분만 만들면 될 것 같습니다.
    // this.state.answer.researchID, this.state.answer.enrolledID이 그 부분입니다.
    let { web3, coinbase, answer } = this.state;
    let researchID = web3.utils.keccak256(DATA.toString());
    let enrolledID = web3.utils.keccak256(new Date().toString());

    console.log("researchID", researchID);
    console.log("enrolledID", enrolledID);

    let self = this;

    this.setState(
      {
        answer: update(answer, {
          researchID: { $set: researchID },
          enrolledID: { $set: enrolledID }
        })
      },
      () => {
        web3.eth.personal
          .sign(
            web3.utils.keccak256(JSON.stringify(this.state.answer)),
            coinbase
          )
          .then(signature => {
            console.log("signature", signature);
            self.upload(signature);
          });
      }
    );
  };

  upload = async signature => {
    let obj = this.state.answer;
    obj.signature = signature;
    obj.researcher = this.state.coinbase;

    let url =
      "https://us-central1-research-server-76ca9.cloudfunctions.net/survey-fill";
    let options = {
      method: "POST",
      mode: "cors",
      headers: {
        "Content-Type": "Application/JSON",
        "Access-Control-Request-Method": "POST"
      },
      body: JSON.stringify(obj)
    };
    let response = await fetch(url, options);
    console.log(response);

    let responseOK = response && response.ok;
    if (responseOK) {
      let data = await response.json();
      console.log(data);
      // do something with data

      /* callback func */
      let answer = this.newResearch();
      this.setState({ answer, auth: false }, () => {
        this.props.toggleScreen();
        alert("제출 완료");
      });
    }
  };

  onShowAlert = (msg, color, auth) => {
    this.setState({ visible: true, msg, color, auth }, () => {
      window.setTimeout(() => {
        this.setState({ visible: false, msg: "", color: "" });
      }, 2000);
    });
  };

  render() {
    let steps = [];
    const survey = DATA;

    let formkey = 0;
    let radiokey = 0;

    for (let i = 0, cnt = 0; i < survey.steps.length; i++) {
      const step = survey.steps[i];
      if (step.surveys) {
        let content = [];
        for (const [j, item] of step.surveys.entries()) {
          switch (item.type) {
            case 0:
              content.push(
                <FormGroup
                  key={formkey++}
                  controlId={"formBasicText" + cnt + "_" + j}
                >
                  <ControlLabel>{this.getQuestion(j, item)}</ControlLabel>
                  <FormControl
                    type="text"
                    name={item.type + ":" + cnt + ":" + j}
                    onChange={this.handleChange}
                  />
                </FormGroup>
              );
              break;
            case 1:
              content.push(
                <FormGroup
                  key={formkey++}
                  controlId={"formControlsTextarea" + cnt + "_" + j}
                >
                  <ControlLabel>{this.getQuestion(j, item)}</ControlLabel>
                  <FormControl
                    rows="5"
                    componentClass="textarea"
                    bsClass="form-control"
                    name={item.type + ":" + cnt + ":" + j}
                    onChange={this.handleChange}
                  />
                </FormGroup>
              );
              break;
            case 2:
              if (!item.group) {
                let answer = [];
                let etc = null;
                for (const [k, a] of item.answer.entries()) {
                  if (a !== "") {
                    answer.push(
                      <div key={k}>
                        <label>
                          <input
                            type="radio"
                            name={item.type + ":" + cnt + ":" + j}
                            value={k}
                            onChange={this.handleChange}
                          />
                          &nbsp;{a}
                        </label>
                      </div>
                    );
                  } else {
                    etc = true;
                  }
                }
                if (!etc) {
                  content.push(
                    <FormGroup key={formkey++}>
                      <div>
                        <ControlLabel>{this.getQuestion(j, item)}</ControlLabel>
                      </div>
                      {answer}
                    </FormGroup>
                  );
                } else {
                  content.push(
                    <FormGroup key={formkey++}>
                      <div>
                        <ControlLabel>{this.getQuestion(j, item)}</ControlLabel>
                      </div>
                      {answer}
                      <FormGroup>
                        <ControlLabel>기타</ControlLabel>
                        <FormControl
                          type="text"
                          name={item.type + ":" + cnt + ":" + j}
                          onChange={this.handleChange}
                        />
                      </FormGroup>
                    </FormGroup>
                  );
                }
              } else {
                let group = [];
                for (const [k, g] of item.group.entries()) {
                  let answer = [];
                  for (const [l, a] of item.answer.entries()) {
                    answer.push(
                      <Radio
                        name={item.type + ":" + cnt + ":" + j + ":" + k}
                        value={l}
                        inline
                        onChange={this.handleChange}
                        key={radiokey++}
                      >
                        {a}
                      </Radio>
                    );
                  }
                  group.push(
                    <Row key={k}>
                      <Col sm={2}>
                        <ControlLabel>{g}</ControlLabel>
                      </Col>
                      <Col sm={9}>{answer}</Col>
                    </Row>
                  );
                }
                content.push(
                  <FormGroup key={formkey++}>
                    <div>
                      <ControlLabel>{this.getQuestion(j, item)}</ControlLabel>
                    </div>
                    {group}
                  </FormGroup>
                );
              }
              break;
            case 3:
              if (!item.group) {
                let answer = [];
                let etc = null;
                for (const [k, a] of item.answer.entries()) {
                  if (a !== "") {
                    answer.push(
                      <div key={k}>
                        <label>
                          <input
                            type="checkbox"
                            name={item.type + ":" + cnt + ":" + j}
                            value={k}
                            inline="true"
                            onChange={this.handleChange}
                          />
                          &nbsp;{a}
                        </label>
                      </div>
                    );
                  } else {
                    etc = true;
                  }
                }
                if (!etc) {
                  content.push(
                    <FormGroup key={formkey++}>
                      <div>
                        <ControlLabel>{this.getQuestion(j, item)}</ControlLabel>
                      </div>
                      {answer}
                    </FormGroup>
                  );
                } else {
                  content.push(
                    <FormGroup key={formkey++}>
                      <div>
                        <ControlLabel>{this.getQuestion(j, item)}</ControlLabel>
                      </div>
                      {answer}
                      <FormGroup>
                        <ControlLabel>기타</ControlLabel>
                        <FormControl
                          type="text"
                          name={item.type + ":" + cnt + ":" + j}
                          onChange={this.handleChange}
                        />
                      </FormGroup>
                    </FormGroup>
                  );
                }
              } else {
                // not yet!
              }
              break;
            case 4:
              let select = [];
              for (const [k, a] of item.answer.entries()) {
                if (k === 0) {
                  select.push(
                    <label key={k}>
                      {a}&nbsp;
                      <input
                        type="radio"
                        name={item.type + ":" + cnt + ":" + j}
                        value={k}
                        onChange={this.handleChange}
                      />
                      &nbsp;&nbsp;
                    </label>
                  );
                } else {
                  select.push(
                    <label key={k}>
                      <input
                        type="radio"
                        name={item.type + ":" + cnt + ":" + j}
                        value={k}
                        onChange={this.handleChange}
                      />
                      &nbsp;{a}&nbsp;
                    </label>
                  );
                }
              }
              content.push(
                <FormGroup key={formkey++}>
                  <div>
                    <ControlLabel>{this.getQuestion(j, item)}</ControlLabel>
                  </div>
                  {select}
                </FormGroup>
              );
              break;
            default:
              break;
          }
        }
        steps.push(
          <Panel key={i}>
            <Panel.Heading>
              <h3>{step.title}</h3>
              <div>{step.desc}</div>
            </Panel.Heading>
            <Panel.Body>{<form>{content}</form>}</Panel.Body>
          </Panel>
        );
        cnt++;
      } else {
        steps.push(
          <Panel key={i}>
            <Panel.Heading>
              <h3>{step.title}</h3>
            </Panel.Heading>
          </Panel>
        );
      }
    }

    return (
      <div className="section section-basic" id="basic-elements">
        <img alt="..." className="path" src={require("assets/img/path1.png")} />
        <Container>
          <Row style={{ marginBottom: 100 }}>
            <Col md="4">
              <hr className="line-info" />
              <h1>
                Verifiable Credential
                <span className="text-info"> 조사원 인증</span>
              </h1>
            </Col>
          </Row>
          <Row>
            <Col>
              <Card className="card-coin card-plain" style={{ minHeight: 300 }}>
                <CardHeader>
                  <img
                    alt="..."
                    className="img-center img-fluid"
                    src={require("assets/img/card.png")}
                  />
                </CardHeader>
                <CardBody>
                  <Row>
                    <Col className="text-center" md="12">
                      <h4 className="text-uppercase">QR CODE</h4>
                      <hr className="line-info" />
                    </Col>
                  </Row>
                  <Row>
                    <ListGroup>
                      <ListGroupItem>CAMERA</ListGroupItem>
                      <ListGroupItem>
                        <Button
                          className="btn-simple"
                          color="info"
                          onClick={() => {
                            this.onCamera();
                          }}
                        >
                          ON
                        </Button>
                      </ListGroupItem>
                    </ListGroup>
                  </Row>
                </CardBody>
                <CardFooter className="text-center">
                  {this.state.camera ? (
                    <QrReader
                      delay={300}
                      onError={this.handleError}
                      onScan={this.handleScan}
                      style={{ width: "100%" }}
                    />
                  ) : (
                    false
                  )}
                </CardFooter>
              </Card>
            </Col>
          </Row>
        </Container>
        {this.state.auth ? (
          <Container style={{ maxWidth: "1300px" }}>
            <div className="content">
              <Panel>
                <Panel.Heading>
                  <h3>{DATA.title}</h3>
                </Panel.Heading>
                <Panel.Body>{DATA.desc}</Panel.Body>
                <Panel.Footer>{DATA.author}</Panel.Footer>
              </Panel>
              {steps}
              <Button onClick={this.submit}>제출</Button>
            </div>
          </Container>
        ) : (
          false
        )}

        <Alert color={this.state.color} isOpen={this.state.visible}>
          {this.state.msg}
        </Alert>
      </div>
    );
  }
}

export default Basics;
