import React from "react";
// import { Link } from "react-router-dom";
// reactstrap components
import {
  Button,
  Collapse,
  NavbarBrand,
  Navbar,
  NavItem,
  Nav,
  Container,
  Row
} from "reactstrap";
import "assets/css/nucleo-icons.css";
import getWeb3 from "utils/getWeb3";

class ComponentsNavbar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      collapseOpen: false,
      color: "navbar-transparent",
      etherNet: "Unknown Network",
      etherExplorer: ""
    };
  }
  componentDidMount = async () => {
    window.addEventListener("scroll", this.changeColor);

    const web3 = await getWeb3();

    let etherNet = await web3.eth.net.getId();
    let etherExplorer;

    if (etherNet === 3) {
      etherNet = "Ropsten Test Network";
      etherExplorer = "https://ropsten.etherscan.io/address/";
    } else if (etherNet === 1) {
      etherNet = "Main Ethereum Network";
      etherExplorer = "https://etherscan.io/address/";
    }

    this.setState({ etherNet, etherExplorer });
  };
  componentWillUnmount() {
    window.removeEventListener("scroll", this.changeColor);
  }
  changeColor = () => {
    if (
      document.documentElement.scrollTop > 99 ||
      document.body.scrollTop > 99
    ) {
      this.setState({
        color: "bg-info"
      });
    } else if (
      document.documentElement.scrollTop < 100 ||
      document.body.scrollTop < 100
    ) {
      this.setState({
        color: "navbar-transparent"
      });
    }
  };
  toggleCollapse = () => {
    document.documentElement.classList.toggle("nav-open");
    this.setState({
      collapseOpen: !this.state.collapseOpen
    });
  };
  onCollapseExiting = () => {
    this.setState({
      collapseOut: "collapsing-out"
    });
  };
  onCollapseExited = () => {
    this.setState({
      collapseOut: ""
    });
  };
  scrollToDownload = () => {
    document
      .getElementById("download-section")
      .scrollIntoView({ behavior: "smooth" });
  };
  render() {
    return (
      <Navbar
        className={"fixed-top " + this.state.color}
        color-on-scroll="100"
        expand="lg"
      >
        <Container>
          <div className="navbar-translate">
            <NavbarBrand
              data-placement="bottom"
              to="/"
              rel="noopener noreferrer"
              title="Designed and Coded by Creative Tim"
            >
              <span>Research System • </span>
              신산머루
            </NavbarBrand>
          </div>
          <Collapse
            className={"justify-content-end " + this.state.collapseOut}
            navbar
            isOpen={this.state.collapseOpen}
            onExiting={this.onCollapseExiting}
            onExited={this.onCollapseExited}
          >
            <div className="navbar-collapse-header">
              <Row></Row>
            </div>
            <Nav navbar>
              <NavItem>
                <Button className="nav-link d-none d-lg-block" color="default">
                  <i className="tim-icons icon-single-02" />
                  {this.props.ethrDid}
                </Button>
              </NavItem>
              <NavItem>
                <Button
                  className="nav-link d-none d-lg-block"
                  color="warning"
                  href={this.state.etherExplorer + this.props.account}
                  target="blank"
                >
                  <i className="tim-icons icon-world" />
                  Ethereum: {this.state.etherNet} (
                  {this.props.etherBalance.toString().substring(0, 4)} ether)
                </Button>
              </NavItem>
            </Nav>
          </Collapse>
        </Container>
      </Navbar>
    );
  }
}

export default ComponentsNavbar;
