import React from "react";

// core components
import IndexNavbar from "components/Navbars/IndexNavbar.jsx";
import PageHeader from "components/PageHeader/PageHeader.jsx";
import Footer from "components/Footer/Footer.jsx";

// sections for this page/view
import Basics from "views/IndexSections/Basics.jsx";

import getWeb3 from "utils/getWeb3";

import EthrDID from "ethr-did";

import LoadingOverlay from "react-loading-overlay";

class Index extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      accounts: "",
      klayBalance: 0,
      etherBalance: 0,
      ethrDid: "",
      loadingScreen: false
    };
  }
  componentDidMount = async () => {
    document.body.classList.toggle("index-page");

    try {
      // Get network provider and web3 instance.
      const web3 = await getWeb3();

      // Use web3 to get the user's accounts.
      const accounts = await web3.eth.getAccounts();

      // create DID
      const ethrDid = await new EthrDID({
        provider: web3.currentProvider,
        address: accounts[0]
      });

      let etherBalance = web3.utils.fromWei(
        await web3.eth.getBalance(accounts[0])
      );

      if (this.getCurrentProvider(web3)) {
        const self = this;

        window.ethereum.on("accountsChanged", async accounts => {
          etherBalance = web3.utils.fromWei(
            await web3.eth.getBalance(accounts[0])
          );

          self.setState({
            accounts,
            etherBalance,
            ethrDid: ethrDid.did
          });
        });
      }

      this.setState({
        accounts,
        etherBalance,
        ethrDid: ethrDid.did
      });
    } catch (error) {
      // Catch any errors for any of the above operations.
      alert(
        `Failed to load web3, accounts, or contract. Check console for details.`
      );
      console.error(error);
    }
  };
  componentWillUnmount() {
    document.body.classList.toggle("index-page");
  }

  getCurrentProvider = web3js => {
    if (web3js.currentProvider.isMetaMask) {
      return "metamask";
    }
    if (web3js.currentProvider.isTrust) {
      return "trust";
    }
    if (web3js.currentProvider.isGoWallet) {
      return "goWallet";
    }
    if (web3js.currentProvider.isAlphaWallet) {
      return "alphaWallet";
    }
    if (web3js.currentProvider.isStatus) {
      return "status";
    }
    if (web3js.currentProvider.isToshi) {
      return "coinbase";
    }
    if (typeof window.__CIPHER__ !== "undefined") {
      return "cipher";
    }
    if (web3js.currentProvider.constructor.name === "EthereumProvider") {
      return "mist";
    }
    if (web3js.currentProvider.constructor.name === "Web3FrameProvider") {
      return "parity";
    }
    if (
      web3js.currentProvider.host &&
      web3js.currentProvider.host.includes("infura")
    ) {
      return "infura";
    }
    if (
      web3js.currentProvider.host &&
      web3js.currentProvider.host.includes("localhost")
    ) {
      return "localhost";
    }
    return null;
  };

  toggleScreen = () => {
    this.setState({ loadingScreen: !this.state.loadingScreen });
  };

  render() {
    return (
      <LoadingOverlay
        active={this.state.loadingScreen}
        spinner
        text="Saving on IPFS and Ethereum..."
      >
        <IndexNavbar
          account={this.state.accounts[0]}
          etherBalance={this.state.etherBalance}
          ethrDid={this.state.ethrDid}
        />
        <div className="wrapper">
          <PageHeader />
          <div className="main">
            <Basics
              getCurrentProvider={this.getCurrentProvider}
              ethrDid={this.state.ethrDid}
              toggleScreen={this.toggleScreen}
            />
            {/* <Navbars />
            <Tabs />
            <Pagination />
            <Notifications />
            <Typography />
            <JavaScript />
            <NucleoIcons />
            <Signup />
            <Examples />
            <Download /> */}
          </div>
          <Footer />
        </div>
      </LoadingOverlay>
    );
  }
}

export default Index;
